.header {
  padding: 25px 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  width: 100%;
  // .header__container

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.nav {
  display: flex;
  align-items: center;
  // .nav__list

  &__list {
    display: flex;
    gap: 65px;
  }

  // .nav__link

  &__link {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.02em;
    color: var(--white);
    transition: 0.5s;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      bottom: -8px;
      left: 0;
      width: 0;
      height: 2px;
      background: var(--orange);
      transition: 0.5s;
    }
    &:hover {
      color: var(--orange);
    }
    &:hover::before {
      width: 100%;
    }
  }

  // .nav__btn

  &__btn {
    margin-left: 160px;
    padding: 22px 30px;
    background: rgba(233, 215, 167, 0.1);
    border: 1px solid var(--orange);
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    color: var(--orange);
    transition: 0.5s;
    &:hover {
      background: var(--orange);
      color: var(--black);
    }
    @include tablet {
      margin-left: 100px;
    }
  }
  @include small-tablet {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: #251928;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translateX(-100%);
    transition: 0.5s all;
    z-index: 10;
    &__list {
      flex-direction: column;
      align-items: center;
    }
    &__btn {
      margin-left: 0;
      margin-top: 50px;
    }
  }
}
.burger {
  display: none;
  z-index: 50;
  @include burger;
  @include small-tablet {
    display: block;
  }
}
.menu--active {
  transform: translateX(0);
}
