.auctions {
  padding: 50px 0;
  // .auctions__container

  &__container {
    margin-top: 45px;
    padding-left: max(15px, calc((100% - 1440px) / 2));
  }

  // .auctions__title

  &__title {
    margin: 0;
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 130px;
    color: var(--darkWhite);
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: -30px;
      width: 110px;
      height: 110px;
      background: #8613a5;
      border-radius: 100%;
      z-index: -1;
    }
    @include mobile {
      font-size: 36px;
    }
  }

  // .auctions__nav

  &__nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 28px;
  }

  // .auctions__nav-prev

  &__nav-prev {
    img {
      transform: rotate(-180deg);
    }
    &.swiper-button-disabled {
      opacity: 0.4;
    }
  }

  // .auctions__nav-next

  &__nav-next {
    &.swiper-button-disabled {
      opacity: 0.4;
    }
  }

  //.auctions__slide

  &__item {
    padding: 25px 28px;
    border: 1px solid #000000;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }

  // .auctions__nft

  &__nft {
    img {
      width: 100%;
      height: 360px;
    }
  }

  // .auctions__user

  &__user {
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  // .auctions__user-info

  &__user-info {
    display: flex;
    align-items: center;
  }

  // .auctions__user-photo

  &__user-photo {
    width: 54px;
    height: 54px;
    margin-right: 16px;
    @include mobile {
      margin-bottom: 15px;
      width: 42px;
      height: 42px;
    }
  }

  // .auctions__user-name

  &__user-name {
    margin: 0;
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 35px;
    color: var(--darkWhite);
    margin-bottom: 8px;
    @include mobile {
      font-size: 18px;
      margin-bottom: 4px;
    }
  }

  // .auctions__user-by

  &__user-by {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 35px;
    display: flex;
    align-items: center;
    color: var(--grayish);
    @include mobile {
      font-size: 14px;
    }
  }

  // .auctions__like

  &__like {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 35px;
    color: var(--darkGrayish);
    svg {
      width: 28px;
      fill: var(--darkGrayish);
      height: 25px;
    }
    @include mobile {
      font-size: 24px;
      svg {
        width: 24px;
        height: 22px;
      }
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-top: 28px;
  }

  // .auctions__price-bid

  &__price-bid {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 35px;
    color: var(--dark);
    @include mobile {
      font-size: 20px;
    }
  }

  // .auctions__price-item

  &__price-item {
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 35px;
    color: var(--darkWhite);
  }

  // .auctions__btn

  &__btn {
    margin-top: 40px;
    background: rgba(233, 215, 167, 0.1);
    border: 1px solid #fde8b3;
    width: 100%;
    padding: 21px 0;
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 35px;
    letter-spacing: 0.05em;
    color: var(--white);
    transition: 0.5s;
    &:hover {
      background: var(--orange);
      color: var(--black);
    }
  }
}
