.drop {
  padding: 50px 0;
  background: url(../img/bg.png);
  @include bg-cover;
  // .drop__container

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  // .drop__title

  &__title {
    margin: 0;
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 130px;
    text-align: center;
    color: var(--darkWhite);
    margin-bottom: 25px;
    @include small-tablet {
      font-size: 27px;
      line-height: 65px;
    }
  }
}

.form {
  max-width: 680px;
  display: inline-flex;
  border: 1px solid #e7b226;
  @include small-tablet {
    flex-direction: column;
  }
  // .form__label

  &__label {
    width: 100%;
  }

  // .form__input

  &__input {
    outline: none;
    width: 100%;
    padding: 30px 50px;
    background: transparent;
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 35px;
    color: var(--darkGrayish);
    &::placeholder {
      font-family: "Work Sans";
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 35px;
      color: var(--darkGrayish);
    }
  }

  // .form__btn

  &__btn {
    padding: 30px 63px;
    background: var(--orange);
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 35px;
    color: var(--black);
  }
}
