// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "sans-serif";
  --content-width: 1440px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white: #fff;
  --orange: #ffce4e;
  --dark: #9b9e9a;
  --darkWhite: #f5fbf2;
  --black: #000;
  --grayish: #bdaac1;
  --darkGrayish: #76607a;
  --moderate: #d53d5c;
}
