.categories {
  padding: 150px 0;
  // .categories__container

  &__container {
    padding-left: max(15px, calc((100% - 1440px) / 2));
  }

  // .categories__title

  &__title {
    margin: 0;
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 130px;
    color: var(--darkWhite);
    position: relative;
    margin-bottom: 50px;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: -30px;
      width: 110px;
      height: 110px;
      background: #8613a5;
      border-radius: 100%;
      z-index: -1;
    }
    @include mobile {
      font-size: 36px;
    }
  }

  //.categories__slide

  &__item {
    padding: 25px 28px;
    border: 1px solid #000000;
    background: #3d2942;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }

  // .categories__nft

  &__nft {
    position: relative;
    img {
      width: 100%;
      height: 360px;
    }
    &:hover {
      .categories__nft-btn {
        opacity: 1;
      }
    }
  }

  // .categories__nft-btn

  &__nft-btn {
    opacity: 0;
    transition: 0.5s;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    &__link {
      padding: 21px 57px;
      background: #ffce4e;
      backdrop-filter: blur(22.5px);
      font-family: "Work Sans";
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 28px;
      text-align: center;
      color: var(--black);
    }
  }

  // .categories__user

  &__user {
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  // .categories__user-info

  &__user-info {
    display: flex;
    align-items: center;
  }

  // .categories__user-photo

  &__user-photo {
    width: 54px;
    height: 54px;
    margin-right: 16px;
    @include mobile {
      margin-bottom: 15px;
      width: 42px;
      height: 42px;
    }
  }

  // .categories__user-name

  &__user-name {
    margin: 0;
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 35px;
    color: var(--darkWhite);
    margin-bottom: 8px;
    @include mobile {
      font-size: 18px;
      margin-bottom: 4px;
    }
  }

  // .categories__user-by

  &__user-by {
    margin: 0;
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 35px;
    display: flex;
    align-items: center;
    color: var(--grayish);
    @include mobile {
      font-size: 14px;
    }
  }

  // .categories__like

  &__like {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 35px;
    color: var(--grayish);
    svg {
      width: 28px;
      fill: var(--grayish);
      height: 25px;
    }
    @include mobile {
      font-size: 24px;
      svg {
        width: 24px;
        height: 22px;
      }
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-top: 28px;
  }

  // .categories__price-bid

  &__price-bid {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 35px;
    color: var(--dark);
    @include mobile {
      font-size: 20px;
    }
  }

  // .categories__price-item

  &__price-item {
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 35px;
    color: var(--darkWhite);
  }

  // .categories__btn

  &__btn {
    margin-top: 40px;
    background: rgba(233, 215, 167, 0.1);
    border: 1px solid #fde8b3;
    width: 100%;
    padding: 21px 0;
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 35px;
    letter-spacing: 0.05em;
    color: var(--white);
  }
  .swiper-slide {
    opacity: 0.4;
    transition: opacity 0.3s;
  }
  .swiper-slide-active,
  .swiper-slide-prev,
  .swiper-slide-next {
    opacity: 1;
    transition: opacity 0.3s;
  }
}
