.footer {
  padding: 120px 0 150px 0;
  position: relative;
  // .footer__container

  &__container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include tablet {
      justify-content: center;
    }
  }

  // .footer__left

  &__left {
    display: flex;
    @include tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  // .footer__info

  &__info {
    max-width: 395px;
    margin-left: 50px;
    @include tablet {
      margin-bottom: 70px;
      margin-left: 0;
    }
  }

  // .footer__text

  &__text {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 36px;
    color: var(--darkGrayish);
    margin-bottom: 60px;
    @include tablet {
      text-align: center;
    }
    @include small-tablet {
      font-size: 18px;
    }
  }

  // .footer__social

  &__social {
    display: flex;
    gap: 35px;
    margin-bottom: 60px;
    @include tablet {
      position: absolute;
      bottom: 60px;
      left: 50%;
      transform: translateX(-50%);
      margin: 0;
    }
  }

  // .footer__social-item

  &__social-item {
    svg {
      fill: var(--moderate);
      width: 40px;
      height: 40px;
      transition: 0.3s;
      &:hover {
        fill: var(--grayish);
      }
    }
  }

  // .footer__email

  &__email {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 36px;
    color: var(--darkGrayish);
    @include tablet {
      position: absolute;
      bottom: 0;
      left: 50%;
      width: 100%;
      text-align: center;
      transform: translateX(-50%);
      margin-bottom: 0;
    }
  }

  // .footer__right

  &__right {
    display: flex;
    gap: 220px;
    @include mobile {
      width: 100%;
      gap: 50px;
      justify-content: space-between;
    }
  }

  // .footer__title

  &__title {
    margin: 0;
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 35px;
    color: var(--darkWhite);
    margin-bottom: 18px;
  }

  // .footer__list

  &__list {
    margin-bottom: 15px;
  }

  // .footer__link

  &__link {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 50px;
    color: var(--darkGrayish);
    transition: 0.3s;
    &:hover {
      color: var(--moderate);
    }
    @include mobile {
      font-size: 18px;
    }
  }

  .logo {
    img {
      width: 194px;
      height: 194px;
    }
    @include tablet {
      margin-bottom: 50px;
    }
  }
}
