.hero {
  padding-top: 140px;
  position: relative;
  @include small-tablet {
    padding-bottom: 150px;
  }
  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 45%;
    height: 100%;
    background: rgba(79, 70, 78, 0.45);
    backdrop-filter: blur(15px);
    z-index: -1;
    @include small-tablet {
      width: 100%;
      height: 50%;
      bottom: 0;
      top: auto;
    }
  }
  // .hero__container

  &__container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;
    @include small-tablet {
      justify-content: center;
    }
  }

  // .hero__content

  &__content {
    max-width: 710px;
    @include small-tablet {
      max-width: 342px;
    }
  }

  // .hero__title

  &__title {
    margin: 0;
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 85px;
    line-height: 130px;
    color: var(--darkWhite);
    margin-bottom: 77px;
    span {
      color: var(--orange);
    }
    @include small-tablet {
      font-size: 42px;
      text-align: center;
      line-height: 63px;
    }
  }

  // .hero__btn

  &__btn {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 48px;
    margin-bottom: 145px;
    @include small-tablet {
      justify-content: center;
      gap: 20px;
      margin-bottom: 70px;
    }
  }

  // .hero__btn-item

  &__btn-item {
    padding: 24px 75px;
    background: rgba(233, 215, 167, 0.1);
    border: 1px solid var(--orange);
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 36px;
    color: var(--white);
    transition: 0.5s all;
    &:hover {
      background: var(--orange);
      color: var(--black);
    }
    @include small-tablet {
      padding: 16px 35px;
      font-size: 18px;
    }
  }

  // .hero__list

  &__list {
    display: flex;
    align-items: center;
    gap: 103px;
    @include small-tablet {
      justify-content: center;
      gap: 40px;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }
  }

  // .hero__list-item

  &__list-item {
    text-align: center;
  }

  // .hero__list-sum

  &__list-sum {
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 62px;
    line-height: 78px;
    color: var(--darkWhite);
    @include small-tablet {
      font-size: 32px;
    }
  }

  // .hero__list-text

  &__list-text {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 35px;
    color: var(--dark);
    @include small-tablet {
      font-size: 16px;
    }
  }
}
