.creators {
  padding: 50px 0;

  // .creators__title

  &__title {
    margin: 0 auto;
    max-width: 428px;
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 130px;
    color: var(--darkWhite);
    position: relative;
    margin-bottom: 80px;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: -30px;
      width: 110px;
      height: 110px;
      background: #8613a5;
      border-radius: 100%;
      z-index: -1;
    }
    @include mobile {
      font-size: 36px;
    }
  }

  // .creators__body

  &__body {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 45px;
    column-gap: 70px;
  }

  // .creators__item

  &__item {
    min-width: 370px;
    flex: 1 1 40%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 26px 29px;
    background: #302034;
    box-shadow: inset 2px 0px 0px #e03054;
    @include mobile {
      min-width: 290px;
    }
  }

  // .creators__user

  &__user {
    display: flex;
    align-items: center;
  }

  // .creators__user-photo

  &__user-photo {
    margin-right: 24px;
    img {
      width: 104px;
      height: 104px;
      @include tablet {
        width: 70px;
        height: 70px;
      }
    }
  }

  // .creators__name

  &__name {
    margin: 0;
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 35px;
    letter-spacing: 0.05em;
    color: var(--darkWhite);
    @include tablet {
      font-size: 20px;
    }
  }

  // .creators__price

  &__price {
    margin: 0;
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--grayish);
    @include tablet {
      font-size: 14px;
    }
  }

  // .creators__items

  &__items {
    padding: 5px 0 5px 45px;
    border-left: 3px solid #49374e;
    @include mobile {
      padding-left: 15px;
    }
  }

  // .creators__num

  &__num {
    text-align: center;
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 35px;
    letter-spacing: 0.05em;
    color: var(--darkWhite);
    @include tablet {
      font-size: 24px;
    }
  }

  // .creators__text

  &__text {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--grayish);
    @include tablet {
      font-size: 16px;
    }
  }
}
