.works {
  padding: 70px 0;
  // .works__container

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  // .works__title

  &__title {
    margin: 0;
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 130px;
    margin-bottom: 25px;
    color: var(--darkWhite);
    @include mobile {
      font-size: 36px;
    }
  }

  // .works__content

  &__content {
    display: flex;
    justify-content: space-between;
    width: 100%;
    @media (max-width: 1180px) {
      flex-wrap: wrap;
      gap: 50px;
    }
    @include mobile {
      flex-direction: column;
      gap: 128px;
    }
  }

  // .works__item

  &__item {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
  }

  // .works__item-icon

  &__item-icon {
    max-width: 106px;
    max-height: 106px;
  }

  // .works__item-text

  &__item-text {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 28px;
    text-transform: capitalize;
    color: var(--orange);
  }
  // .work-item__1

  &-item__1 {
    &::after {
      content: "";
      background: url(../img/line.svg);
      @include bg-cover;
      position: absolute;
      right: -170px;
      top: 30%;
      width: 165px;
      height: 10px;
      @media (max-width: 1330px) {
        width: 120px;
        right: -120px;
      }
      @media (max-width: 1180px) {
        display: none;
      }
      @include mobile {
        display: block;
        top: auto;
        bottom: -40px;
        height: 3px;
        right: auto;
        transform: rotate(90deg);
        width: 75px;
      }
    }
  }

  // .work-item__2

  &-item__2 {
