.collections {
  padding: 50px 0;
  // .collections__container

  &__container {
    padding-left: max(15px, calc((100% - 1440px) / 2));
  }

  // .collections__title

  &__title {
    margin: 0;
    font-family: "Oxanium";
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 130px;
    color: var(--white);
    position: relative;
    margin-bottom: 100px;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: -30px;
      width: 110px;
      height: 110px;
      background: #e03054;
      border-radius: 100%;
      z-index: -1;
    }
    @include mobile {
      font-size: 36px;
    }
  }

  // .collections__swiper

  &__swiper {
    position: relative;
  }

  // .collections__item

  &__item {
    padding: 36px;
    background: #3d2942;
    @include mobile {
      padding: 20px;
    }
  }

  // .collections__img
  &__img {
    img {
      width: 100%;
    }
  }

  // .collections__user

  &__user {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
  }

  // .collections__user-info

  &__user-info {
    display: flex;
    align-items: center;
    gap: 15px;
  }

  // .collections__user-descr

  &__user-descr {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 36px;
    color: var(--darkWhite);
    @include mobile {
      font-size: 20px;
    }
  }

  // .collections__user-text

  &__user-text {
    font-family: "Work Sans";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 35px;
    color: var(--grayish);
    @include mobile {
      font-size: 16px;
    }
  }

  // .collections__swiper-arrow

  &__swiper-arrow {
    position: absolute;
    top: 50%;
    right: 100px;
    z-index: 50;
    @include small-tablet {
      display: none;
    }
  }
  .swiper-slide {
    opacity: 0.4;
    transition: opacity 0.3s;
  }
  .swiper-slide-active,
  .swiper-slide-prev {
    opacity: 1;
    transition: opacity 0.3s;
  }
}
